import React, { useEffect, useState } from "react";
import "./App.css";
import { cloneDeep, mapValues } from "lodash";
import { Message, Page, Sidebar, SidebarItem } from "./components";
import {
  actions,
  FlowChart,
  IChart,
  INodeInnerDefaultProps,
} from "@mrblenny/react-flow-chart";
import { chartScheme } from "./misc/chartScheme";
import styled from "styled-components";
import { Button } from "antd";

import {
  AmazonOutlined,
  DeleteOutlined,
  EditOutlined,
  FunctionOutlined,
} from "@ant-design/icons";

const Outer = styled.div`
  padding: 30px;
`;

const Input = styled.input`
  padding: 10px;
  border: 1px solid cornflowerblue;
  width: 100%;
`;

/**
 * Create the custom component,
 * Make sure it has the same prop signature
 */
const NodeInnerCustom = ({ node, config }: INodeInnerDefaultProps) => {
  switch (node.type) {
    case "Function input":
    case "Function output":
    case "Storage provider":
      return (
        <Outer>
          <p>{node.type}</p>
        </Outer>
      );
    default:
      return (
        <Outer>
          <p>{node.type}</p>
          <br />
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-around",
            }}
          >
            <Button
              type="primary"
              shape="circle"
              icon={<EditOutlined />}
            ></Button>

            {/* <Button danger shape="circle" icon={<DeleteOutlined />}></Button> */}
          </div>
          {/* <Input
            type="number"
            placeholder="Some Input"
            onChange={(e) => console.log(e)}
            onClick={(e) => e.stopPropagation()}
            onMouseUp={(e) => e.stopPropagation()}
            onMouseDown={(e) => e.stopPropagation()}
          /> */}
        </Outer>
      );
  }
};
export class App extends React.Component {
  public state = cloneDeep(chartScheme);
  public render() {
    const chart = this.state;
    const stateActions = mapValues(actions, (func: any) => (...args: any) =>
      this.setState(func(...args))
    ) as typeof actions;
    return (
      <div className="App">
        <Page>
          <FlowChart
            chart={chart}
            callbacks={stateActions}
            Components={{
              NodeInner: NodeInnerCustom,
            }}
          />
          <Sidebar>
            <Message>Drag and drop these items onto the canvas.</Message>
            <SidebarItem
              display={
                <div>
                  <FunctionOutlined /> input
                </div>
              }
              type="Function input"
              ports={{
                port1: {
                  id: "port1",
                  type: "bottom",
                  properties: {
                    custom: "property",
                  },
                },
                port2: {
                  id: "port2",
                  type: "right",
                  properties: {
                    custom: "property",
                  },
                },
              }}
            />
            <SidebarItem
              display={
                <div>
                  <FunctionOutlined /> output
                </div>
              }
              type="Function output"
              ports={{
                port1: {
                  id: "port1",
                  type: "top",
                  properties: {
                    custom: "property",
                  },
                },
                port2: {
                  id: "port2",
                  type: "bottom",
                  properties: {
                    custom: "property",
                  },
                },
              }}
            />
            <SidebarItem
              type="S3 storage"
              ports={{
                port1: {
                  id: "port1",
                  type: "left",
                  properties: {
                    custom: "property",
                  },
                },
              }}
              properties={{
                custom: "property",
              }}
            />
            <SidebarItem
              type="One-data storage"
              ports={{
                port1: {
                  id: "port1",
                  type: "left",
                  properties: {
                    custom: "property",
                  },
                },
              }}
              properties={{
                custom: "property",
              }}
            />
            <SidebarItem
              display={
                <div>
                  oscar <FunctionOutlined />
                </div>
              }
              type="Function"
              ports={{
                port1: {
                  id: "port1",
                  type: "top",
                  properties: {
                    custom: "property",
                  },
                },
                port2: {
                  id: "port2",
                  type: "bottom",
                  properties: {
                    custom: "property",
                  },
                },
              }}
            />
            <SidebarItem
              display={
                <div>
                  aws <FunctionOutlined />
                </div>
              }
              type="Function"
              ports={{
                port1: {
                  id: "port1",
                  type: "top",
                  properties: {
                    custom: "property",
                  },
                },
                port2: {
                  id: "port2",
                  type: "bottom",
                  properties: {
                    custom: "property",
                  },
                },
              }}
            />
          </Sidebar>
        </Page>
      </div>
    );
  }
}
